/<template>

   <transition name ="fade">
     <Modal :Rooms = "roomdatas" :isShowModal = "isShowModal" :ciickIndex = "ciickIndex" @closeModal="isShowModal = false"/>
   </transition> 

   <Discount :discount_percent = "discount_percent" v-if="isShowDiscount == true"/>

   <div v-for="(roomdata,i) in roomdatas" :key="i">
     <img :src="roomdata.image" class="room-img">
     <h4 :style="style_blue" @click="showDetail(roomdata.id)">{{roomdata.title}}</h4>
     <p>{{roomdata.price}}</p>
     <button @click="increaseSingoCount(roomdata.id)">허위매물신고</button> 
     <span style="margin-left: 20px;margin-bottom: 10px;">신고수 : {{roomdata.singocount}}</span>
   </div>
   <br>
   <br>
</template>

<script>

import Modal from './Modal.vue';
import Discount from './Discount.vue';
import {oneroomdatas} from '.././assets/oneroomdatas';

export default {
    name: 'Onerooms_',
    data() {
        return {
            isShowDiscount : true,
            ciickIndex : 0,
            isShowModal : false,
            roomdatas : oneroomdatas,
            discount_percent: 30,
        }
    },
    methods: {
        increaseSingoCount(index) {
            this.roomdatas[index].singocount += 1
        },
        showDetail(index) {
            this.isShowModal = true;
            this.ciickIndex = index;
        },
    },
    mounted() {
        var interval = setInterval(() => {
            this.discount_percent--;

            if(this.discount_percent < 1) {
                clearInterval(interval);
                this.isShowDiscount=false
            }
        },1000);
    },
    components: {
        Modal,
        Discount,
    }
}
</script>

<style>

</style>