import { createWebHistory
    //createWebHashHistory
    , createRouter } from "vue-router";
import Onerooms from './components/Onerooms.vue';
import MakePersons from './components/MakePersons.vue';


const routes = [
    {
        path: "/TestVue/",
        components: {
            Onerooms,
          },
    },
    {
        path: "/TestVue/list",
        components: {
            Onerooms,
          },
    },
    {
        path: "/TestVue/persons",
        components: {
            MakePersons,
          },
    },
];

const router = createRouter({
history: createWebHistory(),
routes,
});


export default router; 